: #3399ff;\n                cursor: pointer;\n            }\n            button:hover{\n                background-color: #008aff;\n            }\n        }\n\n    }\n    .navbar{\n    \n    justify-content: space-between;\n\n    ul{\n        @extend .dFlex;\n        margin:0;\n        padding:0;\n        list-style-type: none;\n\n        li{\n            font-size: 14px;\n            margin-left: 15px;\n            padding: 10px;\n            box-sizing: border-box;\n            \n            cursor: pointer;\n            a{\n                color: $white;\n                text-decoration: none;\n            }\n            \n        }\n        li:hover{\n            text-decoration: underline;\n        }\n    }\n    .cart{\n        color:rgba($white, .7);\n        text-decoration: none;\n        .fa{\n            font-size: 20px;\n            margin-left: 15px;\n            padding: 10px;\n            cursor: pointer;\n            &:hover{\n                color: $red; \n            }\n        }\n\n    }\n    }\n}\n\n.main_content{\n    @extend .dFlex;\n    position: relative;\n    width: 70%;\n    height: 100%;\n    margin: 50px auto;\n    padding: 50px 0 0;\n    box-sizing: border-box;\n    justify-content: space-between;\n    flex-wrap: wrap;\n\n    .card{\n        width: 30%;\n        border-radius: 15px;\n        @extend .dFlex;\n        flex-direction: column;\n        position: relative;\n        transform:scale(.8);\n        transition: all .3s ease-in-out;\n        margin-bottom: 50px;\n        a{\n            @extend .dFlex;\n            justify-content: center;\n            flex-direction: column;\n            .card_img{\n                margin-top:-5vw;\n                img{\n                    width:200px;\n                }\n            }\n            .card_header{\n                @extend .dFlex;\n                flex-direction: column;\n                color:$dark_gray;\n                margin: 25px 0;\n                h2{\n                    font-size: 18px;;\n                }\n                p{\n                    font-size: 14px;\n                    text-align: center;\n                    color: $dark_gray;\n                    margin: 8px 0;\n                } \n                .price{\n                    font-size: 25px;\n                    \n                    span{\n                        font-size: 15px;\n                        display: inline-block;\n                        vertical-align: top;\n                    }\n                }\n            }\n        }\n    }\n    .card:hover{\n        background-color: transparent;\n        transform: scale(1);\n        cursor:pointer;\n    }\n\n}\n\n.btn{\n    width:130px;\n    height:35px;\n    font-size: 14px;\n    border-radius: 35px;\n    color: $white;\n    background-color:$text;\n    @extend .dFlex;\n    cursor: pointer;\n    position: relative;\n    transition: background-color .3s ease;\n    overflow: hidden;\n}\n\n.btn:hover{\n    \n        background-color: $red;\n        color: $white;\n    \n}\n\n.footer{\n    @extend .dFlex;\n    width:100%;\n    height: 300px;\n    justify-content: center;\n    margin-top: auto;\n    color:rgba($white,.3);\n    font-size: 12px;\n    background-color: $dark_gray;\n    .social{\n        @extend .dFlex;\n       margin-left: 50px;\n        .fa { \n            justify-content: space-between;\n            width:60px;\n            height:60px;\n            color:rgba($white, .3);\n            font-size: 40px;\n            cursor: pointer;\n            margin-right: 50px;\n        }\n        .fa:hover{\n            color: $red;\n        }\n    }\n}\n\n.logo{\n    width: 240px;\n    margin-right: 50px;\n}"],"sourceRoot":""}]);
